.hero {
    overflow: hidden;
    &__inner {
        position: relative; }
    &__prev-arrow,
    &__next-arrow {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        color: $white;
        z-index: 2;
        cursor: pointer;
        user-select: none;
        .icon {
            display: block;
            font-size: 55px; } }
    &__prev-arrow {
        left: -36px;
        @include r(1760) {
            left: 5px; } }
    &__next-arrow {
        right: -36px;
        @include r(1760) {
            right: 5px; } }
    &__dot {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: $white;
        .slick-initialized li.slick-active & {
            background-color: $green; } }
    .slick-list.draggable {
        overflow: visible; }
    .slick-dots {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 26px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        li {
            line-height: 0;
            &:not(:first-child) {
                margin-left: 18px; } } } }

.hero-slider {
    &:before,
    &:after {
        content: '';
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($brown, .6);
        z-index: 1; }
    &:before {
        left: 100%; }
    &:after {
        right: 100%; }
    &__slide {
        padding: 109px 0 88px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover; }
    &__title {
        padding: 27px 106px 21px 98px;
        background-color: rgba($white, .6);
        max-width: 1032px; }
    &__content {
        padding: 25px 122px 30px 102px;
        max-width: 535px;
        background-color: rgba($brown, .6);
        font-size: 22px;
        font-weight: 500;
        color: $white; }
    &__btn {
        margin-top: 30px; } }
